@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Roboto&display=swap');

:root {
    --text-light: rgba(22,44,78,.39);
    --text-white: #fff;
    --text-dark: #162C4E;
    --bg-light: #f5f5f5;
    --bg-dark: #162C4E;
    --bg-red: #DF2224;
    --bg-white: #fff;
    --bg-blue: #1D4169;
    --backdrop: rgba(18,18,18,.8);
    --backdrop-filter: blur(20px);

    --mobile-padding: 0 20px;
    --tablet-padding: 0 40px;
    --desc-padding: 0 108px;

    --fs-16: 1em;
    --fs-18: 1.125em;
    --fs-20: 1.25em;
    --fs-24: 1.5em;
    --fs-30: 1.875em;
    --fs-32: 2em;
    --fs-36: 2.25em;
    --fs-40: 2.5em;
    --fs-72: 4.5em;

    --font-Poppins: 'Poppins', sans-serif;
    --font-Roboto: 'Roboto', sans-serif;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 16px;
}

button {
    cursor: pointer;
}

a {
    cursor: pointer;
    text-decoration: none;
}

// --------------------------------------------------------------------------------------------------------
// Breakpoints
// --------------------------------------------------------------------------------------------------------
$breakpoints: ( 
    sm: 560px,
    md: 768px,
    lg: 992px,
    xl: 1200px,
);

// media queries mixins
@mixin breakpoint($screen-size) {
    @if map-has-key($breakpoints, $screen-size) {
        @media (max-width: map-get($breakpoints, $screen-size)) {
            @content;
        }
    }

    @else {
    // Debugging
        @error "`#{$screen-size}` has not been declared as a breakpoint."
    }
}